@font-face {
  font-weight: 400;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"), url("../fonts/Roboto-Regular.ttf") format("truetype"); }

@font-face {
  font-weight: 500;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff"), url("../fonts/Roboto-Medium.ttf") format("truetype"); }

@font-face {
  font-weight: 700;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff"), url("../fonts/Roboto-Bold.ttf") format("truetype"); }

:root {
  --basic-font: "Roboto", "Arial", sans-serif;
  --basic-black: #000000;
  --basic-white: #ffffff;
  --basic-carnation: #fb565a;
  --basic-gallery: #eeeeee;
  --basic-outer-space: #283136;
  --basic-cinnabar: #e74246;
  --basic-valencia: #d7373b;
  --basic-silver-chalice: #a6a6a6;
  --basic-black-300: rgba(0, 0, 0, 0.3);
  --basic-white-300: rgba(255, 255, 255, 0.3);
  --basic-swamp-100: rgba(0, 1, 1, 0.1); }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  height: 100%; }

body {
  height: 100%;
  font-weight: 400;
  font-family: var(--basic-font);
  scroll-behavior: smooth; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.main {
  flex-grow: 1; }

.container {
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px; }

.header {
  padding-top: 49px;
  padding-bottom: 78px;
  background: var(--basic-gallery); }

.logo {
  width: 160px;
  height: 66px; }

.logo__image {
  width: 100%;
  height: 100%; }

.logo[href]:hover .logo__image path {
  fill: var(--basic-silver-chalice); }

.navigation {
  display: grid;
  align-items: center;
  grid-template-columns: 160px auto 115px; }

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  max-width: 485px;
  margin-left: 202px;
  padding-top: 15px; }

.navigation__item {
  margin-right: 46px; }

.navigation__link {
  position: relative;
  display: block;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  font-family: inherit;
  color: var(--basic-black);
  text-transform: uppercase;
  text-decoration: none; }

.navigation__link:hover,
.navigation__link:focus {
  color: var(--basic-carnation); }

.navigation__link:active {
  color: var(--basic-black-300); }

.navigation__item--active .navigation__link::before {
  content: "";
  position: absolute;
  top: 43px;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: var(--basic-carnation); }

.cart {
  position: relative;
  display: flex;
  align-items: center;
  padding-top: 15px; }

.cart__icon {
  width: 15px;
  height: 15px; }

.cart__link {
  margin-left: auto;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  font-family: inherit;
  text-align: right;
  color: var(--basic-black);
  text-transform: uppercase;
  text-decoration: none; }

.cart__link:hover,
.cart__link:focus {
  color: var(--basic-carnation); }

.cart__link:active {
  color: var(--basic-black-300); }

.cart--active .cart__link::before {
  content: "";
  position: absolute;
  top: 58px;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: var(--basic-carnation); }

.features {
  padding-bottom: 80px;
  background: var(--basic-gallery); }

.features__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.features__item {
  position: relative;
  display: none; }

.features__item:first-child {
  display: block; }

.features__promo {
  max-width: 538px;
  margin: 0;
  margin-bottom: 25px;
  font-weight: 500;
  font-size: 55px;
  line-height: 55px;
  font-family: inherit;
  color: var(--basic-black); }

.features__description {
  max-width: 400px;
  margin: 0;
  margin-bottom: 38px;
  font-weight: inherit;
  font-size: 16px;
  line-height: 24px;
  font-family: inherit;
  color: var(--basic-outer-space); }

.features__link {
  display: block;
  width: 240px;
  padding: 17px 54px 15px 54px;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  font-family: inherit;
  color: var(--basic-white);
  text-transform: uppercase;
  text-decoration: none;
  background-color: var(--basic-carnation);
  border-radius: 3px; }

.features__link:hover,
.features__link:focus {
  background-color: var(--basic-cinnabar); }

.features__link:active {
  color: var(--basic-white-300);
  background-color: var(--basic-valencia);
  box-shadow: inset 0 3px 0 var(--basic-swamp-100); }

.features__image {
  position: absolute;
  top: -79px;
  right: 3px; }
